.center {
  text-align: center;
  padding: 2rem;
  background-color: rgb(153, 180, 172);
}

.container {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  align-items: stretch;
  justify-content: center;
  align-items: center;
  width: 90%;
}

.delOrder {
  width: 50%;
  padding: 0.5rem;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: rgb(158, 136, 95);
  border: 1px solid black;
  border-radius: 20px;
}
